body {
  background: linear-gradient(to right, #e1f3d9, #3093e4);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0;
}

.pokemon {
    background-color: #eee;
    border-radius: 20px;
    box-shadow: 0 3px 15px rgba(100, 100, 100, 0.5);
    margin: 10px;
    padding: 20px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.pokemon .img-container {
    background-color: rgba(255, 255, 255, 0.6);
    border-radius: 50%;
    width: 130px;
    height: 130px;
    transition: -webkit-transform .5s;
    transition: transform .5s;
    transition: transform .5s, -webkit-transform .5s;
}

.pokemon .img-container img {
    margin-top: 20px;
    max-width: 100%;
}

.pokemon .img-container:hover {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}

.pokemon .info {
    margin-top: 20px;
}

.pokemon .number {
    background-color: rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    font-size: 0.8em;
    padding: 5px 10px;
}

.pokemon .name {
    margin: 15px 7px;
    letter-spacing: 1px;
}

.pokemon .type{
  font-weight: 600;
  font-size: 15px;
}

.type-text{
  padding:5px;
}







